.ant-tabs-bar {
  border-bottom: 1px solid #FFFFFF;
}
.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab {
  padding: 5px;
  border-radius: 8px;
  background-color: #009CDA;
}
.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab-active {
  padding: 5px;
  border-radius: 8px;
  background-color: #009CDA;
}
.ant-tabs-bar .ant-tabs-tab .header-tab {
  line-height: 32px;
  padding: 5px;
}
.ant-tabs-bar .ant-tabs-tab .header-tab .header-number-tab {
  font-weight: bold;
  font-size: 24px;
}
.ant-tabs-bar .ant-tabs-tab .header-tab .header-text-tab {
  padding-left: 5px;
  padding-right: 5px;
}
@media (max-width: 425px) {
  .header-number-tab {
    display: none;
  }
  .header-icon-tab {
    display: none;
  }
}
@media (min-width: 425px) {
  .ant-tabs-bar .ant-tabs-tab {
    padding: 5px;
    border-radius: 8px;
    background-color: #FFFFFF;
    color: #96A8B7;
    margin-right: 2px;
  }
  .ant-tabs-bar .ant-tabs-tab-active {
    padding: 5px;
    border-radius: 8px;
    background-color: #009CDA;
    color: #FFFFFF;
    margin-right: 2px;
  }
  .ant-tabs-bar .ant-tabs-tab-active:hover {
    color: #FFFFFF;
  }
  .ant-tabs-ink-bar {
    background-color: #FFFFFF;
    z-index: -1;
  }
}
